@tailwind base;
@tailwind components;
@tailwind utilities;
@font-face {
  font-family: "Noto Kufi Arabic";
  src: url("./assets/fonts/NotoKufiArabic-Regular.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Noto Kufi Arabic";
  src: url("./assets/fonts/NotoKufiArabic-Medium.ttf") format("truetype");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Noto Kufi Arabic";
  src: url("./assets/fonts/NotoKufiArabic-Bold.ttf") format("truetype");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

body {
  @apply font-arabic;
}